@import url('https://fonts.googleapis.com/css2?family=Inknut+Antiqua:wght@300;400;500;600;700;800;900&display=swap');

@font-face {
  font-family: OCRAE;
  src: url('../public/assets/fonts/OCRA.TTF');
}

* {
  margin: 0px;
  padding: 0px;
  color: var(--secondaryColor);
  user-select: none;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.page {
  width: 100vw;
  height: 100vh;
}

::-webkit-scrollbar {
  width: 6px;
  height: 100vh;
  background-color: var(--primaryColor);
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  border: 1px solid var(--accentColor);
  background-color: var(--secondaryColor);
}

::selection {
  color: var(--primaryColor);
  background-color: var(--accentColor);
}

h1 {
  user-select: text;
  font-family: "Inknut Antiqua", "serif";
  font-size: clamp(40px, 8vw, 100px);
  line-height: 1.2;
}

h2 {
  font-family: "Inknut Antiqua", "serif";
  font-size: clamp(30px, 5vw, 80px);
  line-height: 1.2;
}

p {
  font-family: 'OCRAE', 'Bebas Neue';
  font-size: clamp(15px, 16px, 25px);
  line-height: 1.1;
}

#custom-cursor {
  position: fixed;
  height: 4em;
  width: 4em;
  transform: translate(-50%, -50%);
  border-radius: 100px;
  background-color: rgba(0, 0, 0, 0);
  backdrop-filter: invert();
  pointer-events: none;
  z-index: 100;
}

.expanded {
  animation: expandAnimation 1s forwards 1;
}

@keyframes expandAnimation {
  0% {
    scale: 100%;
  }

  50% {
    scale: 125%;
  }

  100% {
    scale: 100%;
  }
}

.wave {
  animation: waveMove 4s linear 0s infinite;
}

@keyframes waveMove {
  0% {
    translate: 0px;
  }

  100% {
    translate: 1000px;
  }
}

.logo {
  fill: var(--primaryColor);
}

.logo2 {
  fill: var(--secondaryColor);
}